<template>
  <footer class="menu-wrap">
    <router-link to="/" class="menu-item">
      <i class="ion-md-home"></i>
      <p class="menu-title">首页</p>
    </router-link>
    <router-link to="/dynamic" class="menu-item">
      <i class="ion-md-people"></i>
      <p class="menu-title">动态</p>
    </router-link>
    <router-link to="/search" class="menu-item">
      <i class="ion-ios-search"></i>
      <p class="menu-title">发现</p>
    </router-link>
    <router-link to="/mine" class="menu-item">
      <i class="ion-md-person"></i>
      <p class="menu-title">我的</p>
    </router-link>
  </footer>
</template>
<script>
export default {
  name: "FooterWrap",
  data() {
    return {};
  },
  methods: {
    gotoPage(path) {
      this.$router.push(path);
    }
  }
};
</script>
<style lang="scss" scoped>
.menu-wrap {
  display: flex;
  right: 0;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  border-top: 1px solid $gray;
  background-color: $white;
  padding: 5px 0;
  z-index: 9999;
  .menu-item {
    -webkit-box-flex: 1;
    display: block;
    flex: 1;
    font-size: 14px;
    &.router-link-exact-active {
      color: $primary;
    }
    [class^="ion-"] {
      font-size: 22px;
    }
  }
}
</style>



