import Vue from 'vue'
import App from './App'
import router from './router'
import './style/index.scss'
import 'ionicons/dist/css/ionicons.css'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



