<template>
  <div class="mine-wrap">
    <header>
      <span>我的</span>
      <i class="ion-ios-settings"></i>
    </header>
    <div class="main-container">
      <div class="cell">
        <div class="cell-item person-info">
          <img src="~images/avatar.jpg" width="80">
          <p>
            点击登录账号
            <br>
            <span class="gray">登录后，换手机也不怕丢数据</span>
          </p>
        </div>
      </div>
      <div class="cell">
        <div class="cell-item">
          <i class="ion-ios-paper"></i>
          <span>我关注的主题</span>
          <i class="ion-ios-arrow-forward"></i>
        </div>
        <div class="cell-item">
          <i class="ion-md-basket" style="color:#fcce07;"></i>
          <span>我的收藏</span>
          <i class="ion-ios-arrow-forward"></i>
        </div>
        <div class="cell-item">
          <i class="ion-ios-mail" style="color:#cfd9da;"></i>
          <span>我的通知</span>
          <i class="ion-ios-arrow-forward"></i>
        </div>
        <div class="cell-item">
          <i class="ion-ios-help-circle" style="color:#5ec84a;"></i>
          <span>帮助与反馈</span>
          <i class="ion-ios-arrow-forward"></i>
        </div>
      </div>
      <div class="cell">
        <div class="cell-item">
          <i class="ion-ios-color-palette" style="color:#00acff;"></i>
          <span>我创建的主题</span>
          <i class="ion-ios-arrow-forward"></i>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Follow",
  data() {
    return {};
  }
};
</script>
<style scoped lang="scss">
.mine-wrap {
  background: #eff3f6;
  height: 100%;
  position: relative;
  overflow: hidden;
}
header {
  text-align: center;
  height: 44px;
  line-height: 44px;
  font-size: 18px;
  font-weight: 500;
  background: $white;
  border-bottom: 1px solid $gray;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  i {
    position: absolute;
    right: 20px;
    font-size: 20px;
  }
}
.main-container {
  position: absolute;
  top: 44px;
  bottom: 52px;
  width: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.cell {
  background-color: $white;
  margin-top: 20px;
  .cell-item {
    height: 50px;
    display: flex;
    align-items: center;
    padding-left: 0.2rem;
    border-bottom: 1px solid $gray;
    i:not([class="ion-ios-arrow-forward"]) {
      font-size: 22px;
      margin-right: 0.4rem;
      display: inline-block;
      width: 0.3rem;
      color: #7c94a0;
    }
    i.ion-ios-arrow-forward {
      font-size: 22px;
      margin-right: 0.4rem;
      color: #7c94a0;
    }
    span {
      -webkit-box-flex: 1;
      flex: 1;
      display: block;
    }
    &:first-child {
      border-top: 1px solid $gray;
    }
  }
}
.cell-item.person-info {
  height: 100px;
  img {
    border-radius: 50%;
    width: 70px;
    height: 70px;
    display: block;
  }
  p {
    flex: 1;
    padding-left: 0.42rem;
    font-size: 18px;
    span {
      color: $gray;
      font-size: 16px;
      line-height: 1.5;
    }
  }
}
</style>



