<template>
  <div class="top">
    <header class="header">
      <form action="">
        <div class="header-search">
          <input type="search">
          <i class="ion-ios-search"></i>
        </div>
        <label for="cameraFile">
          <i class="ion-md-camera"></i>
        </label>
        <input type="file" accept="image/*" capture="camera" style="display:none;" id="cameraFile">
      </form>
    </header>
    <div class="container">
      <router-view/>
      <!-- <p v-for="item in 100" :key="item">这是个测试文本，不要慌 {{item}}</p> -->
    </div>
    <p class="fixedBtn" v-show="fixedBtnShow" @click="wrap.scrollTop=0">
      <i class="ion-ios-arrow-round-up"></i>
    </p>
  </div>
</template>
<script>
export default {
  name: "Home",
  data() {
    return {
      fixedBtnShow: false,
      wrap: null
    };
  },
  mounted() {
    this.wrap = document.querySelector(".container");
    this.scrollEvent();
  },
  methods: {
    scrollEvent() {
      this.wrap.addEventListener("scroll", e => {
        this.fixedBtnShow = this.wrap.scrollTop > 100;
      });
    }
  }
};
</script>
<style scoped lang="scss">
.header {
  padding: 5px 10px 0 10px;
  position: fixed;
  left: 0;
  top: 0;
  height: 40px;
  width: 100%;
  background-color: $white;
  font-size: 14px;
  z-index: 900;
  form {
    height: 100%;
    display: -webkit-flex;
    -webkit-flex-direction: row;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .header-search {
    flex: 1;
    display: block;
    height: 100%;
    position: relative;
    input {
      height: 100%;
      width: 100%;
      border: 1px solid $gray;
      color: #000;
      border-radius: 5px;
      padding-left: 22px;
      margin-right: 10px;
      font-size: 14px;
    }
    i {
      position: absolute;
      left: 5px;
      top: 5px;
      font-size: 20px;
      color: $gray;
    }
  }
  label {
    text-align: center;
    font-size: 22px;
    display: block;
    padding-left: 10px;
    color: $gray-dark;
  }
}

.container {
  top: 45px;
  bottom: 51px;
  width: 100%;
  overflow: auto;
  position: absolute;
  z-index: 10;
  font-size: 14px;
}

.fixedBtn {
  position: fixed;
  right: 14px;
  bottom: 70px;
  font-size: 32px;
  border-radius: 50%;
  background-color: $white;
  width: 32px;
  height: 32px;
  line-height: 32px;
  text-align: center;
  color: $primary;
  border: 1px solid $primary;
  z-index: 999;
}
</style>



